/* Слайдер */
.slick-slider{}
/* Слайдер запущен */
.slick-slider.slick-initialized{}
/* Слайдер с точками */
.slick-slider.slick-dotted{}
/* Ограничивающая оболочка */
.slick-list {
	@media (max-width: 700px) {
		overflow: hidden;
	}
	@media (min-width: 700px) {
		overflow: hidden;
	}
}
/* Лента слайдов */
.slick-track{
	@media (max-width: 700px) {
		display: flex;
	}

	@media (min-width: 700px) {
		display: flex;
		max-height: 80vh;
	}
}
/* Слайд */
.slick-slide{}
/* Слайд активный (показывается) */
.slick-slide.slick-active{}
/* Слайд основной */
.slick-slide.slick-current{}
/* Слайд по центру */
.slick-slide.slick-center{}
/* Клонированный слайд */
.slick-slide.slick-cloned{}
/* Стрелка */
.slick-arrow{

	@media (max-width: 700px) {
		position: absolute;
		top: 30%;
		z-index: 10;
		font-size: 0;
		width: 30px;
		height: 50px;
	}

	@media (min-width: 700px) {
		position: absolute;
		top: 45%;
		z-index: 10;
		font-size: 0;
		width: 30px;
		height: 50px;
	}
}
/* Стрелка влево */
.slick-arrow.slick-prev{
	@media (max-width: 700px) {
		left: 0;
		background-image: url("../img/sl-arrow_l.svg");
		background-position: 0 0;
		background-size: 100%;
		background-repeat: no-repeat;
		border: none;
		background-color: transparent;
	}

	@media (min-width: 700px) {
		left: 0;
		background-image: url("../img/sl-arrow_l.svg");
		background-position: 0 0;
		background-size: 100%;
		background-repeat: no-repeat;
		border: none;
		background-color: transparent;
	}
}
/* Стрелка вправо */
.slick-arrow.slick-next{

	@media (max-width: 700px) {
		right: 0;
		background-image: url("../img/sl-arrow_r.svg");
		background-position: 0 0;
		background-size: 100%;
		background-repeat: no-repeat;
		border: none;
		background-color: transparent;
	}

	@media (min-width: 700px) {
		right: 0;
		background-image: url("../img/sl-arrow_r.svg");
		background-position: 0 0;
		background-size: 100%;
		background-repeat: no-repeat;
		border: none;
		background-color: transparent;
	}
}
/* Стрелка не активная */
.slick-arrow.slick-disabled{}
/* Точки (булиты) */
.slick-dots{
	padding-left: 0;
	@media (max-width: 700px) {
		display: flex;
		align-items: center;
		justify-content: center;
	}

	@media (min-width: 700px) {
		display: flex;
		align-items: center;
		justify-content: center;
		margin-top: 20px;
	}
}
.slick-dots li{
	@media (max-width: 700px) {
		list-style: none;
		margin: 0 10px;
	}

	@media (min-width: 700px) {
		list-style: none;
		margin: 0 20px;
	}
}
/* Активная точка */
.slick-dots li.slick-active{
	@media (min-width: 700px) {
		background-color: transparent;
	}
}

.slick-dots li.slick-active button {
	@media (max-width: 700px) {
		background-color: transparent;
		border: 1px solid black;
	}

	@media (min-width: 700px) {
		background-color: transparent;
		border: 1px solid black;
	}
}

/* Элемент точки */
.slick-dots li button{
	@media (max-width: 700px) {
		font-size: 0;
		width: 10px;
		height: 10px;
		background-color: black;
		border-radius: 100%;
		border: none;
	}

	@media (min-width: 700px) {
		font-size: 0;
		width: 10px;
		height: 10px;
		background-color: black;
		border-radius: 100%;
		border: none;
	}
}