

.contacts {
		display: block;
		padding-top: 50px;
		overflow: hidden;
		background: linear-gradient(90deg,#f70210 0%,#a90abe 72%,#a00cd6 100%);

	@media (min-width: 1270px) {
		display: block;
		width: 45vw;
		padding-top: 150px;
		overflow: hidden;
		background: linear-gradient(90deg,#f70210 0%,#a90abe 72%,#a00cd6 100%);
	}
}

.contacts__title {
	font-family: "Montserrat";
	text-align: center;
	color: white;
	margin-top: 0;

	@media (min-width: 700px) {
		margin-bottom: 50px;
		color: 	white;
	}
}

.contacts__item {
	display: flex;
	flex-direction: column;
	margin-bottom: 25px;

	@media (min-width: 700px) {
		flex-direction: row;
	}
}

.contacts__text {
	font-family: "Montserrat";
	color: white;
	margin: 0;

	@media (min-width: 700px) {
		font-size: 14px;
		text-align: rigth;
		margin-left: 10px;
		margin-right: 10px;
		font-weight: normal;
		color: 	white;
	}
}

.contacts__name {
	font-family: "Montserrat";
	text-transform: uppercase;
	font-weight: bold;
	margin-bottom: 10px;
	color: 	white;

	@media (min-width: 700px) {
		font-family: "Montserrat";
		margin-right: auto;
		padding-left: 5px;
		color: 	white;
	}
}

.contacts__link {
	font-family: "Montserrat";
	text-decoration: none;
	color: white;

	@media (min-width: 700px) {
		text-align: right;
		margin-right: 10px;
		font-size: 20px;
		color: 	white;
	}

	&:hover {
		font-weight: bold;

	}
}

.contacts__list {
	@media (max-width: 700px) {
		display: flex;
		width: 100%;
		padding-left: 0;
		justify-content: space-around;
		margin-bottom: 0;
	}

	@media (min-width: 700px) {
		display: flex;
		width: 100%;
		padding-left: 0;
		margin-left: auto;
		margin-top: 50px;
		margin-right: 15px;
		justify-content: space-around;
	}
}
.contacts__item {
}
.contacts__icon {
	
}

.contacts__href {
	fill: 	white;
	@media (min-width: 700px) {
		fill: 	white;
	}

	&:hover {
		fill: black;
	}
}

.contacts__license {
	text-decoration: none;
	text-align: right;
	color: black;
	margin: 0;

	&:hover {
		color: $hover;
	} 
}