@font-face {
    font-family: 'Oswald';
    src: local('Oswald Bold'), local('Oswald-Bold'),
     url('Oswaldbold.woff2') format('woff2'),
      url('Oswaldbold.woff') format('woff'),
       url('Oswaldbold.ttf') format('truetype');
