.process {
	@media (min-width: 700px) {
	}
}

.process__subtitle {
	@media (max-width: 800px) {
		font-family: "Oswald Light";
		text-align: center;
		font-size: 40px;
	}

	@media (min-width: 800px) {
		font-family: "oswald light";
		font-size: 75px;
		text-align: center;
		margin-bottom: 70px;
	}
}
.process__list {
	list-style: none;
	padding-left: 0;
	position: relative;

	@media (max-width: 800px) {
		margin-right: auto;
		margin-left: auto;
	}
}
.process__item {
	@media (max-width: 800px) {
		display: flex;
		flex-direction: column;
	}

	@media (min-width: 800px) {
		display: flex;
		height: 80vh;
		flex-direction: row-reverse;
	}
}
.process__item1 {

	@media (min-width: 800px) and (max-width: 1150px) {
		background-image: url("../img/img-7-o.jpg");
		background-size: 100% 100%;
		background-position: 0 0;
		background-repeat: no-repeat;

	}

	@media (min-width: 1150px) {
		background-image: url("../img/img-7.jpg");
		background-size: 100%;
		background-position: 0 0;
		background-repeat: no-repeat;
	}
}
.process__img {
	@media (max-width: 800px) {
		width: 100%;
		height: 40vh;
	}

	@media (min-width: 800px) {
		width: 40%;
		height: 40vh;
		align-self: center;
		box-shadow: 0px 2px 13px 0px rgba(0,0,0,0.75);
		border-radius: 15px;
	}
}
.process__block {
	@media (max-width: 800px) {
		background-color: white;
	}

	@media (min-width: 800px) {
		padding-bottom: 15px;
		padding-left: 50px;
		text-align: left;
		width: 60%;
		margin-right: auto;
	}
}
.process__block-1 {
}
.process__title {
		text-transform: uppercase;
		font-family: "Oswald light";
		font-weight: normal;

	@media (max-width: 800px) {
		text-align: center;
		font-size: 30px;
	}
	
	@media (min-width: 800px) and (max-width: 1150px) {
		font-size: 30px;
	}

	@media (min-width: 1150px) {
		font-family: "Oswald light";
		font-size: 45px;
		margin-top: 0;
		font-weight: normal;
		text-align: left;
		margin-top: 25px;
		padding-left: 25px;
		margin-bottom: 55px;
	}
}
.proect {
	@media (min-width: 800px) {
		text-align: left;
		padding-left: 25px;
	}
}
.process__text {
		font-family: "Montserrat";
	@media (max-width: 800px) {
		text-align: center;
	}

	@media (min-width: 800px) and (max-width: 1150px) {
		font-size: 20px;
	}

	@media (min-width: 1150px) {
		font-family: "Montserrat";
		font-size: 26px;
		font-weight: normal;
		padding-left: 25px;
	}
}
.proect__list {
		font-family: "Montserrat";
	@media (max-width: 800px) {
	}

	@media (min-width: 800px) and (max-width: 1150px) {
		font-size: 20px;
		padding-left: 0;
	}

	@media (min-width: 1150px) {
		font-family: "Montserrat";
		font-size: 25px;
		padding-left: 45px;
	}
}


