
body.lock {
	overflow: hidden;
}

.header {
	@media (min-width: 300px) and (max-width: 700px) {
		position: fixed;
		width: 100%;
		top: 0;
		left: 0;
		z-index: 50;

		&:before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: #5e5373;
			z-index: 3;
		}
	}

	@media (min-width: 700px) {
		width: 100vw;
		margin: 0 auto;
		position: fixed;
		z-index: 100;
	}
}
.header__body {
	@media (min-width: 300px) and (max-width: 700px) {
		position: relative;
		display: flex;
		justify-content: space-between;
		height: 50px;
		align-items: center;
	}
}
.header__burger {


	span {
		@media (max-width: 700px) {
			position: absolute;
			background-color: #fff;
			left: 0;
			width: 100%;
			height: 2px;
			top: 12px;
		}
	}

	@media (max-width: 700px) {
		display: block;
		position: relative;
		width: 30px;
		z-index: 3;
		margin-left: 10px;

		&:before,
		&:after {
			content: "";
			background-color: #fff;
			position: absolute;
			width: 100%;
			height: 2px;
			left: 0;
		}
	}

	&:before {
		top: 4px;
	}

	&:after {
		bottom: 2px;
	}

	@media (min-width: 700px) {
		display: none;
	}
}

.header__burger.active {
	span {
		display: none;
		transform: scale(0);
		transition: all 0.3s ease 0s;
	}

	&:before {
		@media (max-width: 700px) {
			transform: rotate(45deg);
			top: -2px;
			transition: all 0.3s ease 0s;


		}
	}

	&:after {
		@media (max-width: 700px) {
			transform: rotate(-45deg);
			bottom: 0px;
			transition: all 0.3s ease 0s;

		}
	}
}
.header__menu {
	@media (max-width: 700px) {
		position: fixed;
		top: -100%;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: #18b5a4;
		padding-top: 70px;
		overflow: auto;
	}
}

.header__menu.active {
	@media (max-width: 700px) {
		top: 0;
		transition: all 0.3s ease 0s;

	}
}

.header__list {
	@media (min-width: 300px) and (max-width: 700px) {
		display: block;
	}

	@media (min-width: 700px) {
		display: flex;
		height: 50px;
		margin-top: 0;
		justify-content: space-around;
		align-items: center;
		list-style: none;
		padding-left: 0;
		background: linear-gradient(90deg,#f70210 0%,#a90abe 72%,#a00cd6 100%);
	}
}
.header__item {
	@media (min-width: 300px) and (max-width: 700px) {
		list-style: none;
		margin-bottom: 20px;

	}
}
