}
.header__link {
	@media (min-width: 300px) and (max-width: 700px) {
		color: #fff;
		text-transform: uppercase;
		font-family: "Arial";
		font-size: 24px;
		text-decoration: none;
	}

	@media (min-width: 700px) {
		font-family: "Arial";
		text-decoration: none;
		color: white;
		text-transform: uppercase;

		&:hover {
			border-bottom: 5px solid white;
		}
	}
}
