
body {
	width: 99.5%;
	margin-top: 0;
	
}



html {
	box-sizing: border-box;
}

img {
	max-width: 100%;
}

body.lock {
	overflow: hidden;
}

*,
*::after,
*::before {
	box-sizing: inherit;
}


.container {
	@include set-container;
}

.visually-hidden {
	@include visually-hidden;
}

.start {
	@media (max-width: 700px) {
		background-color: #fcfcff;
		padding-top: 50px
	}

	@media (min-width: 700px) {
		display: flex;
		background-color: #fcfcff;
		padding-top: 50px;
	}
}

.footer {
	position: relative;
	@media (min-width: 900px) {
		display: flex;
		width: 100%;
		justify-content: space-between;
	}
}
