


.info {

	max-width: 100vw;
	min-height: 400px;

	@media (min-width: 700px) {
		margin-top: 200px;
		margin-bottom: 100px;
	}

}

.info__list {
	padding-left: 0;
	margin: auto;
	list-style: none;

	@media (min-width: 700px) {
		margin-left: 50px;
		margin-right: 50px;
	}
}

.info__item {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.info__title {
	margin-top: 50px;

	@media (max-width: 700px) {
		text-align: center;
		font-family: "Oswald light";
	}

	@media (min-width: 700px) {
		text-align: center;
		font-family: "Oswald light";
		text-transform: uppercase;
		font-weight: normal;
		font-size: 45px;
		margin-bottom: 70px;
	}
}

.info__b {
	font-family: "Montserrat";
	opacity: 1;
	text-align: left;

	@media (min-width: 900px) {
		padding-left: 50px;
		font-size: 24px;
		margin-right: 250px;

	}
}

.info__text {
	font-family: "Montserrat";
	opacity: 1;

	@media (min-width: 300px) and (max-width: 900px) {
		text-align: right;
	}

	@media (min-width: 900px) {
		width: 550px;
		font-size: 20px;

	}
}

.info__item--dark {
		border-top: 5px solid grey;
		border-bottom: 5px solid grey;
}